import service from "auth/FetchInterceptor";
import API_CONSTANTS from "constants/ApiConstant";
import Utils from "utils";
import { HEADERS_NAME_FOR_EXPORT } from "constants/CommonConstants";

const rampUpDownReportService = {};

rampUpDownReportService.preparedHeadersForPDF = (data = []) =>
  data.map(col => {
    return { key: col.dataIndex, value: HEADERS_NAME_FOR_EXPORT[col.dataIndex] };
  });

rampUpDownReportService.preparedDataForExport = (data = [], filterVisibleColumns, forExcel = true) => {
  let headers = {};
  let _data = data.map(record => {
    const _item = {
      account: record?.account,
      region: record?.region,
      benchToBilling: record?.benchToBilling,
      billingToBench: record.billingToBench,
      rampUp: record.rampUp,
      rampDown: record.rampDown,
      newJoinees: record?.newJoinees,
      resignations: record?.resignations,
      globalTransfers: record?.globalTransfers,
    };
    if (filterVisibleColumns) {
      Object.keys(_item).forEach(itemKey => {
        if (!filterVisibleColumns[itemKey]) {
          delete _item[itemKey];
        }
      });
    }
    if (forExcel) headers = Utils.makeHeaderDataForExcel(_item);
    return _item;
  });
  if (forExcel) _data.unshift(headers);
  return _data;
};

rampUpDownReportService.flatResponse = response => {
  return response?.map(record => {
    return {
      account: record?.account,
      region: record?.region,
      benchToBilling: record?.benchToBilling,
      billingToBench: record.billingToBench,
      rampUp: record.rampUp,
      rampDown: record.rampDown,
      newJoinees: record?.newJoinees,
      resignations: record?.resignations,
      globalTransfers: record?.globalTransfers,
    };
  });
};

rampUpDownReportService.getReport = async body => {
  const { path, method } = API_CONSTANTS.GET_RAMP_UP_DOWN_REPORT;
  try {
    const response = await service({
      url: path,
      method,
      data: body,
    });
    if (Utils.isEmpty(response)) {
      return [];
    }
    return response;
  } catch (e) {
    console.error("Error in getting the ramp up down report data.", e);
  }
};

rampUpDownReportService.getReportGraphs = async body => {
  const { path, method } = API_CONSTANTS.GET_RAMP_UP_DOWN_REPORT_GRAPHS;
  try {
    const response = await service({
      url: path,
      method,
      data: body,
    });
    if (Utils.isEmpty(response)) {
      return [];
    }
    return response.records;
  } catch (e) {
    console.error("Error in getting the ramp up down report graph data.", e);
  }
};

export default rampUpDownReportService;
