import { FilterOutlined, MenuFoldOutlined, QuestionCircleTwoTone, SearchOutlined } from "@ant-design/icons";
import {
  Button,
  Col,
  DatePicker,
  Drawer,
  Form,
  message,
  notification,
  PageHeader,
  Popover,
  Row,
  Tabs,
  Tooltip,
} from "antd";
import SearchFilterDropdown from "components/shared-components/SearchFilter";
import TableComponent from "components/shared-components/TableComponent/Index";
import TableMenuItem from "components/shared-components/TableMenuItem";
import {
  DATE_FORMAT,
  FILTER_FORM_FIELDS,
  LIGHT_NAVY_BLUE_COLOR,
  REPORTS_DESCRIPTION,
  SEARCH,
  SORT_DIRECTIONS,
} from "constants/CommonConstants";
import { REPORT_DATE_FORMAT_YYYY_MM_DD } from "constants/DateConstant";
import { DATA_TABLE_COLUMNS, RampUpDOwnFormName, RAMP_FILE_NAME } from "constants/RampUpDownReportConstant";
import { MENU_ITEMS, MENU_ITEMS_TYPES } from "constants/EmployeeLoginDetails";
import useIntlMessage from "hooks/useIntlMessage";
import _ from "lodash";
import moment from "moment";
import { useEffect, useMemo, useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { showLoader } from "redux/actions/Loader";
import rampUpDownReportService from "services/RampUpDownReportService";
import Utils from "utils";
import RampUpDownReportVisual from "./RampUpDownReportVisual";
import { setRampUpDownReportColumns } from "redux/actions";
import contentWrapUpDownReport from "./wrapUpContent";

const RampUpDownReport = () => {
  const dispatch = useDispatch();
  const [open, setOpen] = useState(false);
  const [filteredRampUpDownReport, setFilteredRampUpDownReport] = useState([]);
  const [filteredInfo, setFilteredInfo] = useState({});
  const [sortedInfo, setSortedInfo] = useState({});
  const [employeeExitReportData, setReportData] = useState([]);
  const [loading, setLoading] = useState(false);
  const [visibility, setVisibility] = useState();
  const allLocations = useSelector(state => state.myTeams.allLocations);
  const [isMenuVisible, setIsMenuVisible] = useState(false);
  const [searchParams, setSearchParams] = useState();
  const accountsList = useSelector(store => store.myTeams.accountsList);
  const [openPopOver, setOpenPopOver] = useState(false);
  let searchInput = useRef(null);
  let currentVisibleTableData = [];
  const [tableKey, setTableKey] = useState(0);
  const [form] = Form.useForm();
  const getMessage = useIntlMessage();
  const [startDate, setStartDate] = useState();
  const [endDate, setEndDate] = useState();
  const [activeTabKey, setActiveTabKey] = useState("report");
  const [reportParams, setReportParams] = useState();
  const [graphParams, setGraphParams] = useState();
  const [graphData, setGraphData] = useState([]);
  const [quickActionBtns, setQuickActionBtns] = useState([]);
  const [dynamicColumns, setDynamicColumns] = useState([]);
  const [initialVisibleColumns, setInitialVisibleColumns] = useState([]);
  const [filteredVisibleData, setFilteredVisibleData] = useState([]);
  const visibleFilteredColumns = useSelector(state => state.reports?.rampUpDownReportColumns);
  useEffect(() => {
    return () => {
      dispatch(showLoader(false));
    };
  }, []);
  const fetchRampUpDownReport = async params => {
    try {
      setLoading(true);
      dispatch(showLoader(true));
      const { fromDate, toDate } = params;
      const response = await rampUpDownReportService.getReport({
        fromDate,
        toDate,
      });
      if (!!response?.records?.columns) {
        const { columns, data, quickActions } = response.records;
        let initailColumns = Utils.createObjFromArray(columns);
        if (!Utils.isEmpty(visibleFilteredColumns)) {
          setVisibility(visibleFilteredColumns);
          setInitialVisibleColumns(visibleFilteredColumns);
        } else {
          setVisibility(initailColumns);
          setInitialVisibleColumns(initailColumns);
        }
        setDynamicColumns(columns);
        setReportData(data);
        setQuickActionBtns(quickActions);
        setFilteredRampUpDownReport(rampUpDownReportService.flatResponse(data));
      }
    } catch (e) {
      console.error(e);
    } finally {
      setLoading(false);
      dispatch(showLoader(false));
    }
  };

  const loadData = params => {
    switch (activeTabKey) {
      case "report":
        if (!_.isEqual(params, reportParams)) {
          fetchRampUpDownReport(params);
          setReportParams(params);
        }
        break;
      case "visual":
        if (!_.isEqual(params, graphParams)) {
          fetchGraphs(params);
          setGraphParams(params);
        }
        break;
    }
  };

  const fetchGraphs = async params => {
    try {
      dispatch(showLoader(true));
      const response = await rampUpDownReportService.getReportGraphs(params);
      if (response) {
        setGraphData(response);
      }
    } catch (e) {
      console.error(e);
    } finally {
      dispatch(showLoader(false));
    }
  };

  const getColumnSearchProps = field => {
    let filterObj = { filterIcon: filtered => <SearchOutlined color={filtered ? LIGHT_NAVY_BLUE_COLOR : undefined} /> };
    if (field.type === "select") {
      if (!Utils.isEmpty(field.options)) {
        filterObj.filters = field.options.map(option => ({
          text: getMessage(option.label),
          value: option.value,
        }));
      }
      filterObj.filterIcon = filtered => <FilterOutlined color={filtered ? LIGHT_NAVY_BLUE_COLOR : undefined} />;
      filterObj.onFilter = (value, record) => record[field.key] === value;
    } else {
      filterObj.filterDropdown = ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
        <SearchFilterDropdown
          setSelectedKeys={setSelectedKeys}
          selectedKeys={selectedKeys}
          confirm={confirm}
          clearFilters={clearFilters}
          handleSearch={handleSearch}
          dataIndex={field.key}
          field={field}
          handleReset={handleReset}
          label={field.key}
          dataSource={filteredRampUpDownReport}
        />
      );

      filterObj.onFilter = (value, record) => {
        if (field.type === "date") {
          const valuee = value.format(DATE_FORMAT).toString();
          const timeStampToDate = moment(record[field.key])?.format(DATE_FORMAT)?.toString();
          return timeStampToDate.toLowerCase().includes(valuee.toLowerCase());
        }
        return record[field.key]?.toString()?.toLowerCase().includes(value.toLowerCase());
      };
      filterObj.onFilterDropdownVisibleChange = visible => {
        if (visible) {
          setTimeout(() => searchInput && searchInput.select && searchInput.select());
        }
      };
    }
    return filterObj;
  };

  const generateDynamicColumns = columns =>
    columns.map(field => {
      return {
        title: (
          <Tooltip title={field.title} placement="top">
            <span>{field.title}</span>
          </Tooltip>
        ),
        dataIndex: field.key,
        key: field.key,
        sorter: field.sortable ? (a, b) => Utils.antdTableSorter(a, b, field.key) : null,
        sortDirections: SORT_DIRECTIONS,
        isVisible: visibility[field.key],
        ...getColumnSearchProps(field),
        render: (_, record) => {
          return (
            <Tooltip title={record[field.key]} placement="top">
              <span id={field.key}>{record[field.key]}</span>
            </Tooltip>
          );
        },
      };
    });

  const filteredColumns = useMemo(
    () => generateDynamicColumns(dynamicColumns).filter(item => item.isVisible),
    [visibility, loading],
  );

  const handleChange = (pagination, filters, sorter, extra) => {
    setFilteredVisibleData(extra.currentDataSource);
    setFilteredInfo(filters);
    setSortedInfo(sorter);
  };

  const handleSearch = (selectedKeys, confirm, dataIndex) => {
    confirm();
  };

  const handleReset = (clearFilters, confirm) => {
    clearFilters();
    confirm();
  };
  const onSearch = () => {
    if (!Utils.isEmpty(startDate) && !Utils.isEmpty(endDate)) {
      if (!Utils.validateEndDate(startDate, endDate)) {
        const params = {
          fromDate: startDate.format(REPORT_DATE_FORMAT_YYYY_MM_DD),
          toDate: endDate.format(REPORT_DATE_FORMAT_YYYY_MM_DD),
        };
        setSearchParams(params);
        loadData(params);
      }
    }
  };

  const handleMenuVisibleChange = flag => {
    setIsMenuVisible(flag);
  };

  const onColHide = data => {
    const _visibility = { ...visibility, [data.key]: !visibility[data.key] };
    dispatch(setRampUpDownReportColumns(_visibility));
    setVisibility({ ..._visibility });
  };

  const clearFilters = () => {
    setVisibility(initialVisibleColumns);

    setSearchParams({});
    form.resetFields();
    // setTableKey(tableKey => tableKey + 1);
    setIsMenuVisible(false);
  };

  const onMenuItemClick = ({ key }) => {
    switch (key) {
      case MENU_ITEMS_TYPES.clearAllFilters:
        clearFilters();
        break;
      case MENU_ITEMS_TYPES.exportAllExcel:
      case MENU_ITEMS_TYPES.exportVisibleExcel:
        exportToExcel(key);
        break;
      case MENU_ITEMS_TYPES.exportAllPDF:
        exportToPDF(key);
        break;
      case MENU_ITEMS_TYPES.exportVisiblePDF:
        exportToPDF(key);
        break;
      case MENU_ITEMS_TYPES.exportAllVisibleExcel:
        exportToExcel(key);
        break;
      case MENU_ITEMS_TYPES.exportAllVisiblePDF:
        exportToPDF(key);
        break;
      default:
        return;
    }
  };

  const getDateHeader = () => {
    return `${getMessage("common.label.ramp_up_down_report")} from ${startDate.format("ll")} to ${endDate.format(
      "ll",
    )}`;
  };

  const exportToExcel = type => {
    Utils.exportToExcel({
      type: type,
      dynamicColumns: dynamicColumns,
      reportData: filteredRampUpDownReport,
      visibleReportData: currentVisibleTableData,
      visibility: visibility,
      fileName: RAMP_FILE_NAME,
      currentDataSource: filteredVisibleData.length > 0 ? filteredVisibleData : filteredRampUpDownReport,
      dateHeader: getDateHeader(),
    });
    setIsMenuVisible(false);
  };

  const exportToPDF = type => {
    Utils.exportToPDF({
      type: type,
      dynamicColumns: dynamicColumns,
      reportData: filteredRampUpDownReport,
      visibleReportData: currentVisibleTableData,
      visibleReportColumns: filteredColumns,
      tableDynamicColumns: generateDynamicColumns(dynamicColumns),
      visibility: visibility,
      fileName: RAMP_FILE_NAME,
      currentDataSource: filteredVisibleData.length > 0 ? filteredVisibleData : filteredRampUpDownReport,
      dateHeader: getDateHeader(),
    });
    setIsMenuVisible(false);
  };
  const showDrawer = () => {
    setOpen(true);
  };

  const onClose = () => {
    setOpen(false);
  };

  const disableDatesForEndDate = current => {
    const isFuture = current > moment().endOf("day");

    const monthLater = current.diff(startDate, "months") > 0;

    const lessThanStartDate = current.diff(startDate, "days + 1") <= 0;

    return !!lessThanStartDate || !!isFuture || !!monthLater;
  };

  const disableDatesForStartDate = current => {
    const isFuture = current > moment().endOf("day");

    return !!isFuture || !!Utils.disableDatesTillFeb(current);
  };

  const startDateChangeHandler = val => {
    setStartDate(val);
    setEndDate(null);
    form.setFieldsValue({ toDate: null });
  };

  const handleOpenChange = newOpen => {
    setOpenPopOver(newOpen);
  };

  return (
    <>
      <Row align="middle" className="mb-3">
        <Col xs={24} sm={12} md={12} lg={24} xl={24}>
          <PageHeader
            className="p-2 pl-2"
            title={getMessage("common.label.ramp_up_down_report")}
            subTitle={
              <Popover
                placement="right"
                trigger="click"
                content={contentWrapUpDownReport}
                open={openPopOver}
                onOpenChange={handleOpenChange}
              >
                <QuestionCircleTwoTone className="font-size-md mb-0" />
              </Popover>
            }
          />
        </Col>
      </Row>
      <Form
        form={form}
        name={RampUpDOwnFormName}
        onFinish={onSearch}
        layout="horizontal"
        wrapperCol={{ span: 16 }}
        labelCol={{ span: 8 }}
        labelAlign="left"
      >
        {employeeExitReportData?.length === 0 ? null : (
          <Row justify="space-between" gutter={[20, 20]} className="mt-4">
            <Col lg={24} md={24}>
              <div className="ant-affix mt-2 menu-bar">
                <Button type="primary" onClick={showDrawer} shape="circle" className="mr-3">
                  <MenuFoldOutlined />
                </Button>
              </div>
            </Col>
          </Row>
        )}
        <Row gutter={[20, 5]} className="mb-3">
          <Col xl={8} lg={11} md={12} xs={24}>
            <Form.Item
              label={getMessage(FILTER_FORM_FIELDS.START_DATE.label)}
              name={FILTER_FORM_FIELDS.START_DATE.name}
              rules={[{ required: true }]}
            >
              <DatePicker
                className={"w-100"}
                format={DATE_FORMAT}
                value={startDate}
                disabledDate={disableDatesForStartDate}
                onChange={startDateChangeHandler}
              />
            </Form.Item>
          </Col>
          <Col xl={8} lg={11} md={12} xs={24}>
            <Form.Item
              label={getMessage(FILTER_FORM_FIELDS.END_DATE.label)}
              name={FILTER_FORM_FIELDS.END_DATE.name}
              rules={[{ required: true }]}
            >
              <DatePicker
                className={"w-100"}
                disabled={!startDate}
                format={DATE_FORMAT}
                value={endDate}
                disabledDate={disableDatesForEndDate}
                onChange={val => setEndDate(val)}
              />
            </Form.Item>
          </Col>

          <Col xl={8} lg={24} md={24} xs={24}>
            <Button type="primary" icon={<SearchOutlined className="mr-2" />} htmlType="submit">
              {SEARCH.text}
            </Button>
          </Col>
        </Row>
      </Form>

      <Tabs type="card" onChange={key => setActiveTabKey(key)}>
        <Tabs.TabPane tab="Report" key="report" className="pl-4 pr-3 pt-3" style={{ position: "relative" }}>
          {!!filteredRampUpDownReport.length && !!quickActionBtns.length && (
            <div className="is-mobile">
              <div className="floating-btn floating-buttons">
                {quickActionBtns.map(item => {
                  return (
                    <Tooltip title={item.description} placement="top">
                      <Button
                        type="primary"
                        className="floating-btn-circle my-1"
                        onClick={() => Utils.scrollIntoColumn(item.key)}
                      >
                        {item.title}
                      </Button>
                    </Tooltip>
                  );
                })}
              </div>
            </div>
          )}
          <TableComponent
            tableKey={tableKey}
            rowKey={(record, index) => `${record.employeeId}${index}`}
            columns={filteredColumns}
            data={filteredRampUpDownReport}
            scroll={{ x: true }}
            handleChange={handleChange}
            visibleData={data => {
              currentVisibleTableData = data;
            }}
            tableSize="small"
            visible={isMenuVisible}
            onDropdownMenuChange={handleMenuVisibleChange}
            showSorterTooltip={false}
          />
        </Tabs.TabPane>
        {/* <Tabs.TabPane className="p-3" tab="Visual" key="visual">
          <RampUpDownReportVisual graphData={graphData} />
        </Tabs.TabPane> */}
      </Tabs>

      <Drawer onClose={onClose} visible={open} destroyOnClose width={250}>
        <TableMenuItem
          columns={generateDynamicColumns(dynamicColumns)}
          onColHide={onColHide}
          menuItems={MENU_ITEMS}
          onMenuItemClick={onMenuItemClick}
        />
      </Drawer>
    </>
  );
};

export default RampUpDownReport;
